@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500;700;900&family=Open+Sans&display=swap');

$bg-color: #fff;
$main-cursor: pointer;
%overlay {
    background: #E0E2;
    box-shadow: 0px 6px 59px rgba(53, 49, 84, 0.08);
} 
$base-grey-text: #5C6A79;
$base-black-text: #1C2A39;
$base-purple: #4C3DB2;
$main-shadow: 0px 24px 36px rgba(53, 49, 84, 0.28); 

body {
    font-size: 10px;
}
.container {
    width: 100%;
    background: $bg-color;
    margin: 0 auto;
}

//HEADER
header {
    background-color: #FFF;
    z-index: 10;
    position: sticky;
    top: 0;
    width: 100%;
    
}

.header__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    max-width: 1120px;
    height: 116px;
    
}

.header__logo {
    color: $base-black-text;
    font-family: Montserrat;
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    text-decoration: none;
    z-index: 20;
}

.nav {
    display: flex;
    align-items: center;
    list-style: none;
    padding-left: 0;
    gap: 2rem;
    &-link {
        text-transform: uppercase;
        text-decoration: none;
        font-family: Montserrat;
        font-size: 10px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
        color: $base-grey-text;
        &:hover {
            font-weight: 900;
            color: $base-black-text;
        
        }
    }

}

.header-icons {
    display: flex;
    justify-content: space-between;
    gap: 2rem;
    z-index: 30;
}

.header-icons__bag {
    position: relative;
}
.header-icons__bag-quantity {
    width: 13px;
    height: 13px;
    background-color: #FF353A;
    border-radius: 100%;
    position: absolute;
    right: -3px;
    bottom: -3px;
}

.header-icons__burger {
    display: none;

}

@media (max-width: 767px) {
    .header-icons__burger {
        display: block;
        position: relative;
        width: 20px;
        height: 15px;
        cursor: $main-cursor;
    
        &::before {
            content: '';
            top: 0;
            position: absolute;
            height: 10%;
            width: 100%;
            transition: all 0.3s ease 0s;
            background-color: $base-black-text;
            border-radius: 5px;
        }
    
        span {
            top: 50%;
            transform: scale(1) translate(0px, -50%);
            position: absolute;
            height: 10%;
            width: 100%;
            transition: all 0.3s ease 0s;
            background-color: $base-black-text;
            border-radius: 5px;
        }
    
        &::after {
            content: '';
            bottom: 0;
            position: absolute;
            height: 10%;
            width: 100%;
            transition: all 0.3s ease 0s;
            background-color: $base-black-text;
            border-radius: 5px;
        }

        
    }
    .nav {
        position: fixed;
        top: 0;
        right: -100%;
        width: 30%;     
        background-color: rgba(0, 0, 0, 0.7);
        padding: 110px 15px 0px 0px;
        transition: right 0.3s ease 0s;
        flex-direction: column;  
        align-items: flex-end; 
        border-radius: 5px;
        &::before {
            content: "";
            position: fixed;
            width: 100%;
            top: 0;
            right: 0;
            height: 116px;
            background-color: #FFF;

        }
        &-link {
            text-transform: uppercase;
            text-decoration: none;
            font-family: Montserrat;
            font-size: 14px;
            font-style: normal;
            font-weight: 700;
            line-height: normal;
            color: #fff;
            &:hover {
                font-weight: 900;
                color: $base-grey-text;
            
            }
        }
    
    }
    .nav._active {
        right: 0;
    }
}



//SLIDER content

.slider {
    display: flex;
    justify-content: center;
    align-items: center;

}
.slider-box {
    position: relative;
    margin: 0 auto;
    align-items: center;
    display: flex;
    width: 100%;
    margin-bottom: 17px;
}
.slider-content {
    display: flex;
    text-align: center;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 1440px;
    
}
.slider-content__img {
    width: 100%;
    max-width: 1120px;
    margin: 0 auto;
    transition: transform 0.3s ease; 
    background-size: cover;
    background-position: center center;
    height: 702px;
}

.slider-content__traidin {
    position: absolute;
    top: 79px;
    right: 78px;
    display: flex;
    flex-direction: column;
    width: 149px;
    height: 204px;
    background-color: #9E98DC;
    color: #1C2A39;
    text-decoration: none;
    box-shadow: 0px 24px 36px rgba(53, 49, 84, 0.28);
    z-index: 5;
    cursor: $main-cursor;
    &:hover {
        transform: scale(1.1)
    }
    div {
        padding-left:14%;
        padding-top: 102px;
        width: 100%;
        color: $base-black-text;
        font-family: Montserrat;
        font-size: 18px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
        text-transform: uppercase;
        text-align: left;
    }
    svg {
        padding-left: 14%;
    }
}


.slider-content__top-books {
    
    position: absolute;
    bottom: 70px;
    right: 0px;
    display: flex;
    flex-direction: column;
    width: 137px;
    height: 273px;
    background-color: #FF8FE6;
    color: #1C2A39;
    text-decoration: none;
    box-shadow: 0px 24px 36px rgba(53, 49, 84, 0.28);
    cursor: $main-cursor;
    z-index: 5;
    &:hover {
        transform: scale(1.1)
    }
    div {
        padding-left:14%;
        padding-top: 149px;
        width: 100%;
        color: $base-black-text;
        font-family: Montserrat;
        font-size: 18px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
        text-transform: uppercase;
        text-align: left;
    }
    svg {
        padding-left: 14%;
    }
      
}

.dots {
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-bottom: 105px;
  
}

.dot { 
    opacity: 0.3;
}

.dot:hover {
    opacity: 1 !important;
    transform: scale(1.1)
}

//BOOKS
.books {
    display: flex;
    flex-direction: column;
    width: 100%;

}
.books-content {
    display: flex;
    position: relative;
    flex-wrap: wrap;
}

.books-wrapper {
    display: flex;

  }

//aside

.books-categories {
    width: 416px;
    height: 710px;
    background-color: #EFEEF6;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
    text-transform: capitalize;
    color: #5C6A79;
    z-index: 2;
    ul {
        width: 50%; 
        height: 93%;
        padding-left: 30%;
        margin-top: 45px;
        li {
            cursor: $main-cursor;
            padding-bottom: 23px;
            position: relative;
            list-style: none;
            .btn-category {
                border: none;
                background: none;
                white-space: nowrap;
            }
    
            &:hover,
            &:active {
                color: $base-black-text;
                font-family: Montserrat;
                font-size: 16px;
                font-style: normal;
                font-weight: 700;
                line-height: normal;
                text-transform: capitalize;
    
                &::before {
                    content: '\2022';
                    color: #756AD3;
                    position: absolute;
                    left: -15px;
                }
            }
    
            
        }
    }
}
// book-card
  .book-card {
    display: flex;
    width: 413px; 
    height: 300px;
    justify-content: space-between;
    align-items: center;
    gap: 36px;
  }

  .book-img img {
    width: 212px; 
    height: 300px; 
    box-shadow: 0px 24px 36px rgba(53, 49, 84, 0.28);
    object-fit: cover; 
    object-position: center;
  }
  
 // .book-info {}
 

  
  .book-title {
    font-family: Montserrat;
    font-size: 16px;
    font-weight: 700;
    line-height: 20px;
    letter-spacing: 0%;
    text-align: left;
    color: $base-black-text;
  }
  
  .book-authors {
    color: $base-grey-text; 
    font-family: 'Open Sans';
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
    letter-spacing: 0%;
    text-align: left;
    text-transform: capitalize;    
  }

  .book-rating, .reviews {
    color: $base-grey-text;
    font-family: 'Open Sans';
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
    letter-spacing: 0%;
    text-align: left;
  }
  
  .stars {
    display: flex; /* Вывод звездочек рейтинга в один ряд */
    align-items: center; /* Вертикальное выравнивание звездочек */
  }
  
  .full-star, .half-star, .empty-star {
    margin-right: 4px; /* Расстояние между звездами */
  }
  
  .book-description {
    color: #333; /* Цвет описания книги */
    margin-bottom: 10px; /* Отступ после описания */
  }
  
  .book-price {
    font-weight: bold; /* Жирность шрифта цены */
    color: #333; /* Цвет цены */
    margin-bottom: 15px; /* Отступ после цены */
  }
  
  .buy-button {
    background-color: #5C6A79; /* Фон кнопки покупки */
    color: #fff; /* Цвет текста кнопки покупки */
    padding: 10px 20px; /* Паддинги кнопки */
    border: none; /* Убрать рамку кнопки */
    cursor: pointer; /* Курсор в виде пальца */
    border-radius: 4px; /* Скругление углов кнопки */
    transition: background-color 0.3s; /* Анимация фона кнопки */
  }
  
  .buy-button:hover {
    background-color: #4e5866; /* Смена цвета фона кнопки при наведении */
  }
  


//stars-rating 
.stars {
    display: flex;
    align-items: center;
  }
  
  .star {
    width: 12px; 
    height: 12px; 
    background-size: contain;
    background-repeat: no-repeat;
    display: inline-block;
  }
  
  .full-star {
    background-image: url('../images/icons/Full-Star.svg'); 
  }
  
  .half-star {
    background-image: url('../images/icons/half-star.svg'); 
  }
  
  .empty-star {
    background-image: url('../images/icons/Empty-Star.svg'); 
  }